<template>
  <div class="wrapper">
    <div class="content">
      <div class="row">
        <div class="sider sider-a"></div>
        <div class="button" @click="buy(0)">
          <div class='v-h'></div>
          <text class="button-text">订购大桶装水</text>
          <div class='v-h'></div>
        </div>
      </div>
      <div class="row">
        <div class="sider sider-b"></div>
        <div class="button" @click="buy(1)">
          <div class='v-h'></div>
          <text class="button-text">订购中桶装水</text>
          <div class='v-h'></div>
        </div>
      </div>
      <div class="row">
        <div class="sider sider-c"></div>
        <div class="button" @click="buy(2)">
          <div class='v-h'></div>
          <text class="button-text">订购小桶装水</text>
          <div class='v-h'></div>
        </div>
      </div>
      <text class="text-right">Copyright(C) 2017 Chaos.</text>

    </div>
  </div>
</template>

<script>
  let SocketN = require('./SocketN.js')
  let navigator = weex.requireModule('navigator')
  let modal = weex.requireModule('modal')
  let UrlTools = require('./UrlTools.js')
  const storage = weex.requireModule('storage')
  let SimpleStore = weex.requireModule('simpleStore')
  export default {
    data: function () {
      return {
      }
    },
    methods: {
      buy: function(type) {
        navigator.push({
          url: UrlTools.getUrlBase(weex.config.bundleUrl) + '/dist/postorder.js?type=' + type,
          animated: 'true'
        })
      },
      startWebSocketTask: function(openid) {
        var self = this
        SocketN.startWS(openid, {
          onconnecting: function() {
            console.log('connecting...')
            // self.onopeninfo = 'connecting...'
          },
          onopen: function(e) {
            console.log('websocket open')
            // self.onopeninfo = 'websocket open';
          },
          onmessage: function(e) {
            console.log(e.data)
            // self.onmessage = e.data;
          },
          onerror: function(e) {
            console.error(e.data)
            // self.onerrorinfo = e.data;
          },
          onclose: function(e) {
            console.log(e.code)
            // self.onopeninfo = '';
            // self.oncloseinfo = e.code;
          },
          cnotify: function(data) {
            let order = data.data
            let type = ['大桶水', '中桶水', '小桶水']
            let str = '您订购的' + order.num + '桶[' + type[order.type] + ']即将配送'
            modal.alert({
              message: str,
              okTitle: '确定'
            })
          }
        })
      }
    },
    mounted: function () {
      if(weex.config.env.platform !== 'iOS') {
        storage.getItem('userKey', data => {
          if(result === 'success') {

          }
        })
        navigator.setNavBarTitle('主页')
        navigator.clearNavBarLeftItem()
      } else {
        SimpleStore.getItem('userKey', data => {
          if(result === 'success') {

          }
        })
        navigator.setNavBarTitle({title:'主页'})
      }
    }

  }
</script>

<style>

  .wrapper {
    background-color: #eeeeee;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .content{
    position: absolute;
    top:0px;
    bottom:0px;
    left: 0px;
    right:0px;
    flex-direction: column;
    padding-top: 60px;
  }
  .button{
    flex:1;
    flex-direction: column;
    width: 100%;
    right: 0px;
    background-color: #ffffff;
    border-radius: 10px;
    margin-bottom: 2px;
  }
  .button:active {
    background-color: #eeeeee;
  }
  .row{
    height: 180px;
    flex-direction: row;
  }
  .v-h {
    flex:1;
  }
  .button-text{
    height: 80px;
    text-align: center;
    line-height: 80px;
    color: #000000;
    font-size: 35px;
  }
  .sider {
    width:20px;
  }
  .sider-a {
    background-color: #123456;
  }
  .sider-b {
    background-color: #fed657;
  }
  .sider-c {
    background-color: #97290f;
  }
  .text-right {
    text-align: center;
    padding-top: 80px;
    color: #000000;
    font-size: 28px;
  }
</style>



